import React from 'react'
import BaseLayout from '../components/layouts/BaseLayout'
import { withRouter } from 'next/router'
import axios from 'axios';

class Portfolio extends React.Component {
    
    static getInitialProps({query}){ // Destructurizing the data object and extracting the query param
        const portfolioId = query.id;
        let portfolio = {};

        try{
            const response = axios.get(`https://jsonplaceholder.typicode.com/posts/${portfolioId}`);
        }catch(err){
            console.log(err);
        }

        return {};
    }

    render() {
        return (
            <BaseLayout>
                <h1>This is a Portfolio</h1>
                <h2>{this.props.router.query.id}</h2>
            </BaseLayout>
        )
    }
}

export default withRouter(Portfolio);